import React, { useContext } from "react";
import BillingSetupContext from "../BillingSetupContext";
import { Paper, Input } from "@material-ui/core";
import RequiredAsterisk from "../RequiredAsterisk";
import isEmpty from "../isEmptyFunction";
import { SET_INVOICE_RECEIPT } from "../StateManagment/Actions";


const FaxInvoice = () => {
  const { business, dispatch } = useContext(BillingSetupContext);
  const state = business.invoiceReceipt;
  const handleChange = e => {
    const localState = { ...state };
    localState.Fax.message = e.target.value;
    dispatch({ type: SET_INVOICE_RECEIPT, invoiceReceipt: localState });
  };

  if (business.invoiceReceipt.Fax.isChecked) {
    return (
      <Paper className="child-app-paper" elevation={3}>
        Please enter the Fax number to send invoices to.
        <RequiredAsterisk></RequiredAsterisk>
        <br></br>
        <br></br>
        <Input
          className="main-input"
          placeholder="What is your fax number for invoices?"
          onChange={e => handleChange(e)}
          value={business.invoiceReceipt.Fax.message}
          error={isEmpty(business.invoiceReceipt.Fax.message)}
        ></Input>
      </Paper>
    );
  }
  return <div></div>;
};

export default FaxInvoice;
