import React, { useContext } from "react";
import { Button } from "@material-ui/core";
import BillingSetupContext from "./BillingSetupContext";
import RequiredAsterisk from "./RequiredAsterisk";
import { SET_SUBMITTED } from "./StateManagment/Actions";
import emailMatchesRegex from './emailMatchesRegex'

//filter out each item based on nullity.
const noneAreNull = (...val) =>
  val.filter(el => el === null || el === "" || el === false).length <= 0;
const atLeastOneIsTrue = (...val) => val.filter(el => el === true).length > 0;
const allCheckedHaveMessage = (...vals) => {
  //three dots. Must be destructured here so that it is put into a new array in receiving func
  return noneAreNull(...vals.filter(el => el.isChecked).map(el => el.message));
};
const checkInvoiceReceipt = val => {
  if(val.Email.isChecked && emailMatchesRegex(val.Email.message)){//if the email is checked but the address is incorrect don't allow submission
    return false
  }
  if (
    atLeastOneIsTrue(
      val.Email.isChecked,
      val.Fax.isChecked,
      val.Other.isChecked
    )
  ) {
    return allCheckedHaveMessage(val.Email, val.Fax, val.Other); //maybe here compose into an array and apply to a func
  }
  return false;
};


const billingContactPathComplete = business => {
  const pathIsComplete =
    noneAreNull(
      business.name,
      business.updatePreference,
      business.acctsPayableContact,
      business.billingAddress,
      business.phoneNumber,
      business.emailAddress,
      business.approval
    ) && !emailMatchesRegex(business.emailAddress);
  return pathIsComplete;
};
const paymentMethodPathComplete = business => {
  if (business.autoPayPreference === "Credit Card") {
    //Path for CC update
    return (
      noneAreNull(
        business.name,
        business.updatePreference,
        business.ccNumber,
        business.securityCode,
        business.expDateMonth,
        business.expDateYear,
        business.approval
      ) &&
      checkInvoiceReceipt(business.invoiceReceipt) 
    );
  } else {
    return (
      noneAreNull(
        business.name,
        business.updatePreference,
        business.autoPayPreference,
        business.routingNumber,
        business.accountNumber,
        business.bankName,
        business.bankAddress,
        business.bankCity,
        business.bankState,
        business.bankZip,
        business.approval
      ) &&
      checkInvoiceReceipt(business.invoiceReceipt) 
    );
  }
};

const bothPathsComplete = business => {
  return (
    billingContactPathComplete(business) && paymentMethodPathComplete(business)
  );
};

const agreedYes = business => business.approval === "Yes";

const SubmitButton = () => {
  const { business, dispatch } = useContext(BillingSetupContext);
  const showState = () => {
    console.log(business);
    dispatch({ type: SET_SUBMITTED, submitted: true });
  };
  const showButton = () => <Button onClick={e => showState()}>Submit</Button>;

  if (agreedYes(business)) {
    if (business.updatePreference === "Billing Contact") {
      if (billingContactPathComplete(business)) {
        //later to do both simply combine these with a logical and if the applicationstate.updateQuestion is "both"
        return showButton();
      }
    } else if (business.updatePreference === "Payment Method") {
      if (paymentMethodPathComplete(business)) {
        return showButton();
      }
    } else if (business.updatePreference === "Both") {
      if (bothPathsComplete(business)) {
        return showButton();
      }
    }
  }

  return (
    <div className="please-complete-reqd-fields">
      <div>
        <RequiredAsterisk></RequiredAsterisk> denotes required field.
      </div>
      Not all required fields are filled out correctly. Submission button
      appears once all required fields are completed.
    </div>
  );
};

export default SubmitButton;
