import React, { useContext } from "react";
import {
  Paper,
  Checkbox,
  FormControlLabel,
  FormControl,
  FormGroup
} from "@material-ui/core";
import RequiredAsterisk from "../RequiredAsterisk";
import BillingSetupContext from "../BillingSetupContext";
import { SET_INVOICE_RECEIPT } from "../StateManagment/Actions";


const InvoiceReceipt = () => {
  const { business, dispatch } = useContext(BillingSetupContext);
  const state = business.invoiceReceipt;
  const { Email, Fax, Other } = state;
  const Otherbool = Other.isChecked;
  const Emailbool = Email.isChecked;
  const Faxbool = Fax.isChecked;

  function setState(state, name, checked) {
    const newState = { ...state, [name]: checked };
    return newState;
  }

  function setStateFax(state, checked) {
    const newState = { ...state };
    newState.Fax.isChecked = checked;
    return newState;
  }

  function setStateOther(state, checked) {
    const newState = { ...state };
    newState.Other.isChecked = checked;
    return newState;
  }

  function setStateEmail(state, checked) {
    const newState = { ...state };
    newState.Email.isChecked = checked;
    return newState;
  }

  const handleChange = name => event => {
    let localState = {};
    if (name === "Other") {
      localState = setStateOther(state, event.target.checked);
    } else if (name === "Fax") {
      localState = setStateFax(state, event.target.checked);
    } else if (name === "Email") {
      localState = setStateEmail(state, event.target.checked);
    } else {
      localState = setState(state, name, event.target.checked);
    }
    dispatch({ type: SET_INVOICE_RECEIPT, invoiceReceipt: localState });
  };

  return (
    <div>
      <Paper className="child-app-paper" elevation={3}>
        How would you like to receive your invoices?
        <RequiredAsterisk></RequiredAsterisk>
        <br></br>
        <br></br>
        <div>
          <FormControl component="fieldset">
            <FormGroup>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={business.invoiceReceipt.Email.isChecked}
                    onChange={handleChange("Email")}
                    value={Emailbool}
                  />
                }
                label="E-mail"
              />
              <FormControlLabel
                control={
                  <Checkbox
                    checked={business.invoiceReceipt.Fax.isChecked}
                    onChange={handleChange("Fax")}
                    value={Faxbool}
                  />
                }
                label="Fax"
              />
              <FormControlLabel
                control={
                  <Checkbox
                    checked={business.invoiceReceipt.Other.isChecked}
                    onChange={handleChange("Other")}
                    value={Otherbool}
                  />
                }
                label="Other"
              ></FormControlLabel>
            </FormGroup>
          </FormControl>
        </div>
      </Paper>
    </div>
  );
};
export default InvoiceReceipt;
