import React, { useContext, useState } from "react";
import { Paper, Input } from "@material-ui/core";
import RequiredAsterisk from "../../RequiredAsterisk";
import BillingSetupContext from "../../BillingSetupContext";
import {
  SET_EXP_DATE_MONTH,
  SET_EXP_DATE_YEAR
} from "../../StateManagment/Actions";

const ExpDate = () => {
  const { business, dispatch } = useContext(BillingSetupContext);

  const handleDateMonthChange = date => {
    console.log(date);
    dispatch({ type: SET_EXP_DATE_MONTH, expDateMonth: date });
  };
  const handleDateYearChange = date => {
    console.log(date);
    dispatch({ type: SET_EXP_DATE_YEAR, expDateYear: date });
  };
  return (
    <div>
      {" "}
      <Paper className="child-app-paper" elevation={3}>
        Credit Card Expiration Date<RequiredAsterisk></RequiredAsterisk>
        <br></br>
        <br></br>
        <div onChange={e => handleDateMonthChange(e.target.value)} value={business.ccExpDateMonth}>
          <label>Choose your Expiration Date: </label>
          <br></br>
          <select>
            <option value="">Select Your Month</option>
            <option value="01">January</option>
            <option value="02">February </option>
            <option value="03">March</option>
            <option value="04">April</option>
            <option value="05">May</option>
            <option value="06">June</option>
            <option value="07">July</option>
            <option value="08">August</option>
            <option value="09">September</option>
            <option value="10">October</option>
            <option value="11">November</option>
            <option value="12">December</option>
          </select>
        </div>
        <div onChange={e => handleDateYearChange(e.target.value)} value={business.ccExpDateYear}>
          <select>
            <option value="">Select Your Year</option>
            <option value="20"> 2020</option>
            <option value="21"> 2021</option>
            <option value="22"> 2022</option>
            <option value="23"> 2023</option>
            <option value="24"> 2024</option>
            <option value="25"> 2025</option>
            <option value="26"> 2026</option>
            <option value="27"> 2027</option>
          </select>
        </div>
      </Paper>
    </div>
  );
};
export default ExpDate;

