import React, { useContext } from "react";
import BillingSetupContext from "../BillingSetupContext";
import { Paper, Input } from "@material-ui/core";
import RequiredAsterisk from "../RequiredAsterisk";
import { SET_EMAIL_ADDRESS } from "../StateManagment/Actions";
import emailMatchesRegex from '../emailMatchesRegex'


const EmailAddress = () => {
  const { business, dispatch } = useContext(BillingSetupContext);
  return (
    <Paper className="child-app-paper" elevation={3}>
      Email Address
      <RequiredAsterisk></RequiredAsterisk>
      <br></br>
      <Input
        className="main-input"
        placeholder="What is your email address?"
        onChange={e =>
          dispatch({
            type: SET_EMAIL_ADDRESS,
            emailAddress: e.target.value
          })
        }
        value={business.emailAddress}
        error={emailMatchesRegex(business.emailAddress)}
      ></Input>
    </Paper>
  );
};

export default EmailAddress;