import {
  SET_ACCOUNT_NUMBER,
  SET_ROUTING_NUMBER,
  SET_BANK_ZIP,
  SET_BANK_STATE,
  SET_BANK_CITY,
  SET_BANK_ADDRESS,
  SET_SECURITY_CODE,
  SET_FULL_NAME,
  SET_EXP_DATE_YEAR,
  SET_EXP_DATE_MONTH,
  SET_CC_BILLING_ZIP,
  SET_CC_BILLING_STATE,
  SET_CC_BILLING_CITY,
  SET_INVOICE_RECEIPT,
  SET_AUTOPAY_PREFERENCE,
  SET_PRIMARY_CONTACT_PHONE,
  SET_PRIMARY_CONTACT_EMAIL,
  SET_PRIMARY_CONTACT,
  SET_PHONE_NUMBER,
  SET_FAX_NUMBER,
  SET_EMAIL_ADDRESS,
  SET_BILLING_ZIP,
  SET_BILLING_STATE,
  SET_BILLING_CITY,
  SET_BILLING_ADDRESS,
  SET_SUBMITTED,
  SET_ACCTS_PAYABLE_CONTACT,
  SET_APPROVAL,
  SET_BANK_NAME,
  SET_BUSINESS_NAME,
  SET_CC_BILLING_ADDRESS,
  SET_CC_NUMBER,
  SET_UPDATE_PREFERENCE
} from "./Actions";

const reducer = (state, action) => {
  switch (action.type) {
    case SET_BUSINESS_NAME:
      return {
        ...state,
        name: action.name
      };
    case SET_UPDATE_PREFERENCE:
      return {
        ...state,
        updatePreference: action.updatePreference
      };
    case SET_ACCTS_PAYABLE_CONTACT:
      return {
        ...state,
        acctsPayableContact: action.acctsPayableContact
      };
    case SET_BILLING_ADDRESS:
      return {
        ...state,
        billingAddress: action.billingAddress
      };
    case SET_BILLING_CITY:
      return {
        ...state,
        billingCity: action.billingCity
      };
    case SET_BILLING_STATE:
      return {
        ...state,
        billingState: action.billingState
      };
    case SET_BILLING_ZIP:
      return {
        ...state,
        billingZip: action.billingZip
      };
    case SET_PHONE_NUMBER:
      return {
        ...state,
        phoneNumber: action.phoneNumber
      };
    case SET_FAX_NUMBER:
      return {
        ...state,
        faxNumber: action.faxNumber
      };
    case SET_EMAIL_ADDRESS:
      return {
        ...state,
        emailAddress: action.emailAddress
      };
    case SET_PRIMARY_CONTACT:
      return {
        ...state,
        primaryContact: action.primaryContact
      };
    case SET_PRIMARY_CONTACT_EMAIL:
      return {
        ...state,
        primaryContactEmail: action.primaryContactEmail
      };
    case SET_PRIMARY_CONTACT_PHONE:
      return {
        ...state,
        primaryContactPhone: action.primaryContactPhone
      };
    case SET_APPROVAL:
      return {
        ...state,
        approval: action.approval
      };
    case SET_INVOICE_RECEIPT:
      return {
        ...state,
        invoiceReceipt: action.invoiceReceipt
      };
    case SET_CC_NUMBER:
      return {
        ...state,
        ccNumber: action.ccNumber
      };
    case SET_SECURITY_CODE:
      return {
        ...state,
        securityCode: action.securityCode
      };
    case SET_FULL_NAME:
      return {
        ...state,
        fullName: action.fullName
      };
    case SET_CC_BILLING_ADDRESS:
      return {
        ...state,
        ccBillingAddress: action.ccBillingAddress
      };
    case SET_CC_BILLING_CITY:
      return {
        ...state,
        ccBillingCity: action.ccBillingCity
      };
    case SET_CC_BILLING_STATE:
      return {
        ...state,
        ccBillingState: action.ccBillingState
      };
    case SET_CC_BILLING_ZIP:
      return {
        ...state,
        ccBillingZip: action.ccBillingZip
      };
    case SET_AUTOPAY_PREFERENCE:
      return {
        ...state,
        autoPayPreference: action.autoPayPreference
      };
    case SET_ROUTING_NUMBER:
      return {
        ...state,
        routingNumber: action.routingNumber
      };
    case SET_ACCOUNT_NUMBER:
      return {
        ...state,
        accountNumber: action.accountNumber
      };
    case SET_BANK_NAME:
      return {
        ...state,
        bankName: action.bankName
      };
    case SET_BANK_ADDRESS:
      return {
        ...state,
        bankAddress: action.bankAddress
      };
    case SET_BANK_CITY:
      return {
        ...state,
        bankCity: action.bankCity
      };
    case SET_BANK_STATE:
      return {
        ...state,
        bankState: action.bankState
      };
    case SET_BANK_ZIP:
      return {
        ...state,
        bankZip: action.bankZip
      };
    case SET_EXP_DATE_MONTH:
      return {
        ...state,
        expDateMonth: action.expDateMonth
      };
    case SET_EXP_DATE_YEAR:
      return {
        ...state,
        expDateYear: action.expDateYear
      };
    case SET_SUBMITTED:
      return {
        ...state,
        submitted: action.submitted
      };
    default:
      return state;
  }
};

export default reducer;
